.container {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
}

.simple-text {
  font-size: 18px;
  line-height: 21px;

  text-align: center;

  color: #ADBFC7;
}

.list {
  list-style: none;
  text-align: left;
}

.list-item {
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  
  color: #fff;
  margin: 0 auto 21px 22px;

}

.list-item::before {
  display: inline-block;
  border-radius: 4px;
  margin-right: 22px;
  /* display: flex; */
  /* justify-content: center; */
  /* align-items: center; */
  text-align: center;
  color: #000;
  height: 26px;
  width: 26px;

  font-size: 17px;
}

.list-item--string::before {
  font-family: 'Roboto Mono', monospace;
  content: "â€œ";
  background-color: #FFE177;
  font-weight: normal;
  font-size: 24px;
  line-height: 32px;
}

.list-item--boolean::before {
  content: "tf";
  background-color: #B1C7FF;
}

.list-item--number::before {
  content: "#";
  background-color: #1CDDB4;
}

.string {
  color: #FFE177;
}

.boolean {
  color: #B1C7FF;
}

.number {
  color: #1CDDB4;
}