html {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
}

h1,
p {
  margin: 0;
}

.App {
  display: flex;
  justify-content: center;
  margin-top: 20px;

}

.container {
  text-align: center;
  width: 671px;
  height: 745px;
  background-color: #014E6C;
  overflow: scroll;
}

.navbar {
  display: flex;
  align-items: center;
  padding: 13px 18px 21px;
  gap: 3px;
}

.title {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;

  color: #FFFFFF;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.logo {
  display: inline-block;
  height: 38px;
  width: 39px;
}

.input {
  display: flex;
  text-align: center;
  flex-grow: 1;
  box-sizing: border-box;
  height: 44px;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;

  background: #F2F2F2;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}

input::placeholder {
  justify-content: center;
  align-items: center;
  text-align: center;

  font-weight: bold;
  font-size: 25px;
  line-height: 29px;

  color: rgba(97, 97, 97, 0.28);
}

.form {
  display: flex;
  padding: 0 31px;
  margin-bottom: 36px;
}

.button-submit {
  width: 120px;
  height: 44px;

  font-weight: bold;
  font-size: 16px;
  line-height: 19px;

  text-align: center;

  color: #FFFFFF;

  background: #D22C32;
  border-radius: 0px 5px 5px 0px;
}

.simple-text {
  font-size: 18px;
  line-height: 21px;

  text-align: center;

  color: #ADBFC7;
}